@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
.App {
  text-align: center;
}

.form {
  margin: auto;
  max-width: 600px;
  height: 80px;

  display: flex;
  justify-content: space-around;
}

.error {
  font-size: 8px;
  color: red;
}

.responsiblePerson{margin-top:10px}.responsiblePerson__name{font-size:24px;color:#22a49b}

.todo__title{text-transform:uppercase}.todo__status{color:red;font-style:italic}.todo__status--completed{color:green}

